@mixin filterBrightness() {
    -webkit-filter: brightness(0.3);
    filter: brightness(0.3);
}

@mixin transitionBrightness() {
    -moz-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    -webkit-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.wrapper--tooltip__label {
    @include smalltype();

    margin-top: initial;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('above');
    width: 100%;

    &[data-open='false'] {
        .tooltip__label {
            display: none;
        }
    }

    .tooltip__label {
        color: $color-purewhite;
        padding: $measure--half;
        border-left: $border-style-solid $border-width-thick $color-purewhite;

        a {
            @include anchor-discreet--notransition(
                $color-purewhite,
                $color-gray3,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite
            );
        }

        .tooltip__label__licence {
            display: table;
        }
    }

    .tooltip--image-credit {
        @include smalltype();
        background-color: $color-gray10;
        border: 0;
        color: $color-purewhite;
        font-weight: $font-weight-normal;
        margin: 0;
        padding: $measure--half;
        width: auto;
        pointer-events: auto;
        &:hover {
            @include media('hover') {
                background-color: $color-gray8;
            }
        }

        &:active,
        &:focus {
            outline: 0;

            .icon {
                outline: $border-width-default $border-style-dotted
                    $color-purewhite;
            }
        }

        .icon {
            padding: 0.25rem;
            font-size: $font-size--alpha;
        }
    }
}

.tooltip__overlay {
    @include transitionBrightness();
}

img:has(~ .wrapper--tooltip__label[data-open='true']) {
    @include filterBrightness();
}

.card--news {
    .wrapper--tooltip__label ~ .card__anchor {
        @include transitionBrightness();
    }

    .wrapper--tooltip__label[data-open='true'] ~ .card__anchor {
        @include filterBrightness();
    }
}

.card--pathway {
    .wrapper--tooltip__label ~ a {
        @include transitionBrightness();
    }
    .wrapper--tooltip__label[data-open='true'] ~ a {
        @include filterBrightness();
    }
}

.news__related-news {
    .card--news {
        .wrapper--tooltip__label {
            margin: $border-width-thick;
            width: calc(100% - #{$border-width-thick} - #{$border-width-thick});
        }
    }
}

.finder__results {
    .card--news {
        .wrapper--tooltip__label {
            margin: $border-width-thicker;
            width: calc(
                100% - #{$border-width-thicker} - #{$border-width-thicker}
            );
        }
    }
}

.pathway {
    .pathway__cards {
        .card--pathway {
            position: relative;
            .wrapper--tooltip__label {
                margin: initial;

                a {
                    @include anchor-discreet--notransition(
                        $color-purewhite,
                        $color-gray3,
                        $color-purewhite,
                        $color-purewhite,
                        $color-purewhite
                    );
                    border-left: initial;
                    border-right: initial;
                    border-top: initial;
                }
            }

            a {
                .card--pathway__content {
                    background-color: $color-purewhite;
                }

                @include media('hover') {
                    &:hover {
                        .card--pathway__content {
                            background-color: $color-gray1;
                        }
                    }
                }
            }
        }
    }
}
